* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  place-items: center;
  height: 100vh;
  display: grid;
  overflow: hidden;
}

main {
  width: 100%;
  height: 100%;
  position: relative;
  box-shadow: 0 3px 10px #0000004d;
}

.item {
  z-index: 1;
  background-position: center;
  background-size: cover;
  border-radius: 20px;
  width: 200px;
  height: 300px;
  list-style-type: none;
  transition: transform .1s, left .75s, top .75s, width .75s, height .75s;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  box-shadow: inset 0 20px 30px #ffffff4d;
}

.item:first-child, .item:nth-child(2) {
  box-shadow: none;
  opacity: 1;
  border-radius: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: none;
}

.item:nth-child(3) {
  left: 50%;
}

.item:nth-child(4) {
  left: calc(50% + 220px);
}

.item:nth-child(5) {
  left: calc(50% + 440px);
}

.item:nth-child(6) {
  opacity: 0;
  left: calc(50% + 660px);
}

.content {
  color: #fff;
  text-shadow: 0 2px 8px #0009;
  opacity: 0;
  width: min(30vw, 400px);
  font: 400 .85rem helvetica, sans-serif;
  display: none;
  position: absolute;
  top: 50%;
  left: 3rem;
  transform: translateY(-50%);
}

.content .title {
  text-transform: uppercase;
  font-family: Protest Strike, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.content .description {
  font-optical-sizing: auto;
  text-shadow: none;
  background: #00000080;
  border-radius: 10px;
  margin: 1rem 0 1.5rem;
  padding: 20px;
  font-family: Inter, sans-serif;
  font-size: 1rem;
  line-height: 1.7;
}

.item:nth-of-type(2) .content {
  animation: .75s ease-in-out .3s forwards show;
  display: block;
}

@keyframes show {
  0% {
    filter: blur(5px);
    transform: translateY(calc(75px - 50%));
  }

  100% {
    opacity: 1;
    filter: blur();
  }
}

.prev, .next {
  display: none;
}

.nav {
  z-index: 5;
  -webkit-user-select: none;
  user-select: none;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.nav .btn {
  color: #ffffffb3;
  cursor: pointer;
  background-color: #ff000080;
  border: 2px solid #fff9;
  border-radius: 50%;
  margin: 0 .25rem;
  padding: .75rem;
}

.nav .btn:hover {
  background-color: #ff01014d;
}

@media (width > 650px) and (width < 900px) {
  .content .title {
    font-size: 1rem;
  }

  .content .description {
    font-size: .7rem;
  }

  .item {
    width: 160px;
    height: 270px;
  }

  .item:nth-child(3) {
    left: 50%;
  }

  .item:nth-child(4) {
    left: calc(50% + 170px);
  }

  .item:nth-child(5) {
    left: calc(50% + 340px);
  }

  .item:nth-child(6) {
    opacity: 0;
    left: calc(50% + 510px);
  }
}

@media (width < 650px) {
  .content .title {
    font-size: .9rem;
  }

  .content .description {
    font-size: .65rem;
  }

  .item {
    width: 130px;
    height: 220px;
  }

  .item:nth-child(3) {
    left: 50%;
  }

  .item:nth-child(4) {
    left: calc(50% + 140px);
  }

  .item:nth-child(5) {
    left: calc(50% + 280px);
  }

  .item:nth-child(6) {
    opacity: 0;
    left: calc(50% + 420px);
  }
}

